<template>
  <div id="app">
    <!-- 第一个图表容器 -->
    <div ref="chart1" style="width: 600px; height: 400px; margin: auto;"></div>
    <!-- 第二个图表容器 -->
    <div ref="chart2" style="width: 600px; height: 400px; margin: auto;"></div>
  </div>
</template>

<script>
// 引入 ECharts
import * as echarts from 'echarts'

export default {
  name: 'App',
  mounted() {
    // 初始化第一个图表
    this.initChart1();
    // 初始化第二个图表
    this.initChart2();
  },
  methods: {
    initChart1() {
      const myChart1 = echarts.init(this.$refs.chart1);
      const option1 = {
        title: {
          text: '第一个图表'
        },
        tooltip: {},
        legend: {
          data: ['销量']
        },
        xAxis: {
          data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      };
      myChart1.setOption(option1);
    },
    initChart2() {
      const myChart2 = echarts.init(this.$refs.chart2);
      const option2 = {
        title: {
          text: '第二个图表'
        },
        tooltip: {},
        legend: {
          data: ['销量']
        },
        xAxis: {
          data: ["夹克", "T恤", "短裤", "帽子", "手套", "围巾"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'line', // 改变图表类型为折线图
          data: [15, 30, 26, 40, 30, 50]
        }]
      };
      myChart2.setOption(option2);
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
